import { ComponentChildren } from 'preact';
import { dimensionInterpolations } from '../utils/UNSAFE_interpolations/dimensions';
import type { DimensionProps } from '../utils/UNSAFE_interpolations/dimensions';
import { mergeInterpolations } from '../utils/UNSAFE_mergeInterpolations';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { boxAlignmentInterpolations } from '../utils/UNSAFE_interpolations/boxalignment';
import type { BoxAlignmentProps } from '../utils/UNSAFE_interpolations/boxalignment';
import { flexboxInterpolations } from '../utils/UNSAFE_interpolations/flexbox';
import type { FlexboxProps } from '../utils/UNSAFE_interpolations/flexbox';
import { flexitemInterpolations } from '../utils/UNSAFE_interpolations/flexitem';
import type { FlexitemProps } from '../utils/UNSAFE_interpolations/flexitem';

type StyleProps = DimensionProps & FlexboxProps & FlexitemProps & BoxAlignmentProps;

type Props = StyleProps & {
  children?: ComponentChildren;
};

const baseStyles = cssProps`${{ display: 'flex' }}`;

const interpolations = [
  ...Object.values(dimensionInterpolations),
  ...Object.values(flexboxInterpolations),
  ...Object.values(flexitemInterpolations),
  ...Object.values(boxAlignmentInterpolations)
];

const styleInterpolations = mergeInterpolations<Props>(interpolations);

export const Flex = ({ children, ...props }: Props) => {
  const { class: cls, ...styles } = styleInterpolations(props);
  return (
    <div class={`${baseStyles} ${cls}`} style={styles}>
      {children}
    </div>
  );
};
